import React from "react";
import { Parallax } from "react-parallax";
import test from '../Images/office.jpg'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import m from '../Images/movie.jpg'
import cl from '../Images/calc.jpg'
import ttt from '../Images/tt.jpg'
import rps from '../Images/rock.jpg'
import qz from '../Images/quiz.jpg'
import ps from '../Images/pg.jpg'
import inst from '../Images/insta.jpg'
import './Project.css'
import { Footer } from "./Footer";
export const Project = () =>{
    return (
<div>
     
     <Parallax strength={600} bgImage = {test}>
<div className ='container-three'>
<center><h2 id='hd'>PROJECTS</h2></center>
<div className='contain-cards'>
  <div className='cards'>

<div className='cd-two'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="180"
    image={cl}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
      Calculator 
    </Typography>
    <Typography variant="body2" color="text.secondary">
    HTML , CSS , Javascript
    </Typography> <hr></hr>
    <a href="https://calculator-with-simple-operations.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>

<div className='cd-ten'>
<Card sx={{ minWidth: 280 , minHeight :340}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="160"
    image={m}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
    Movie Recommendation
    </Typography>
    <Typography variant="body2" color="text.secondary">
      HTML , CSS , Javascript , Movies API
    </Typography><hr></hr>
    <a href='https://simple-movie-search-website.netlify.app'>Preview</a>
   </CardContent>
</CardActionArea>
</Card>
</div>

<div className='cd-three'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="180"
    image={ttt}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
      Game - Tic Tac Toe
    </Typography>
    <Typography variant="body2" color="text.secondary">
     HTML , CSS , Javascript
    </Typography><hr></hr>
    <a href="https://simple-game-of-tic-tac-toe.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>


<div className='cd-extra'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="160"
    image={inst}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
      Instagram Account Page Clone
    </Typography>
    <Typography variant="body2" color="text.secondary">
     React
    </Typography><hr></hr>
    <a href="https://clone-of-insta-login-page.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>

<div className='cd-four'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="180"
    image={rps}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
      Rock Paper Scissor
    </Typography>
    <Typography variant="body2" color="text.secondary">
     HTML , CSS , Javascript
    </Typography><hr></hr>
    <a href="https://simple-game-rock-paper-scissor.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>

<div className='cd-five'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="180"
    image={qz}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
     Quiz App 
    </Typography>
    <Typography variant="body2" color="text.secondary">
    HTML , CSS , Javascript
    </Typography><hr></hr>
    <a href="https://website-of-simple-quiz.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>

<div className='cd-six'>
<Card sx={{ minWidth: 280 , minHeight :350}}>
<CardActionArea>
  <CardMedia
    component="img"
    height="160"
    image={ps}
    alt="project"
  />
  <CardContent>
    <Typography gutterBottom variant="h5" component="div">
      Animated Password Generator
    </Typography>
    <Typography variant="body2" color="text.secondary">
      HTML , CSS , Javascript
    </Typography><hr></hr>
    <a href="https://animated-password-generator.netlify.app">
    Preview
  </a>
  </CardContent>
</CardActionArea>
</Card>
</div>

  </div>
</div>
</div>
</Parallax>
<div id='second' className ='parallax-three'>
<center><h2>TECHNOLOGIES</h2></center>
</div>
<Footer />   
</div>
    )
}