import React from "react";
import {Parallax} from 'react-parallax'
import Typewriter from 'typewriter-effect';
import one from '../Images/crystal-ball.jpg'
import './Header.css'
import { Body } from "./Body";



export const Header = () =>{
    return(
<div>
<Parallax strength={600} bgImage = {one}>
<div className ='container-one'>
<div className='contain-first'>
<h2 className='welcome'>Welcome to My Portfolio</h2>
<div className="intro">
<Typewriter options={{
    strings: ['Hello!', 'I am Muskan Sirse','Web Developer'],
    loop: true,
    autoStart: true,
  }}/>
<div className = 'learn'>
<h4 id = 'info' >An Enthusiastic learner.....</h4>
</div>
</div>
</div>

</div>
</Parallax>

  

<div className ='parallax-one'>
<h3>ABOUT ME</h3>
<p>An Independent Web Developer Passionate with their Goals. Currently, I'm a Pre-Final year student at Indore Institute of Science and Technology, competing for a B.Tech in Information Technology. I want to work in a challenging and dynamic environment to keep adding value to myself and contributing to growth and success of the organization, and also trying to explore more things.

</p>

</div>
<Body />
</div>
    )
}