import { Parallax } from "react-parallax";
import React, { useState } from "react";
import three from '../Images/black.png'
import './Connect.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


export const Connect = () =>{
 
  const [inputvalue, setInputvalue] = useState({
    Name:"",
    Email:"",
    Message:""
  });


  const getvalue = (e) => {
    const { name, value } = e.target;
    setInputvalue(() => {
      return {
        ...inputvalue,
        [name]: value
      }
    })

  }
  const sentUserData = async (e) => {
    e.preventDefault();

    const{Name,Email,Message} = inputvalue;
    if(Name === ""){
      toast.error("name is required")
    }else if(Email === ""){
      toast.error("email is required")
    }else if(!Email.includes("@")){
      toast.error("invalid email")
    }else if(Message === ""){
      toast.error("fill all fields")
    }else{
      const res = await fetch("http://localhost:3002/register", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          Name, Email, Message
        })
      });
      const data = await res.json();

      if (data.status === 201) {
        toast.success("Your Response has been Submitted");
        setInputvalue({
          ...inputvalue,
          Name:"",
          Email:"",
          Message:""
        })
      }
      else{
        toast.error("email already exists")
      }
      
    }
  }

    return(
<div>
<Parallax strength={600} bgImage = {three}>
<div className ='container-three'>

<div className='contain-form'>
<div className='container'>
<form class="row g-3" method="POST" onChange={getvalue}>
<div class="mb-3">
  <label for="validationDefault01" class="form-label">Name</label>
  <input
    type="text"
    class="form-control"
    id="validationDefault01"
    placeholder='name'
    required=""
    name="Name"
    value={inputvalue.Name}
    onChange={getvalue}
  />
</div>


<div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Email</label>
  <input type="email" class="form-control" id="exampleFormControlInput1" name="Email" value={inputvalue.Email}
  onChange={getvalue} placeholder="name@example.com"/>
</div>

<div class="mb-3">
  <label for="exampleFormControlTextarea1" class="form-label">Message</label>
  <textarea placeholder=' write your message here' name="Message" value={inputvalue.Message}
  onChange={getvalue} class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
</div>

<div class="col-12">
  <button class="btn btn-primary" onClick={sentUserData}  type="submit">Submit</button>
</div>
</form>
<ToastContainer />
</div>
</div>
</div>
</Parallax>
<div id='second-one' className ='parallax-three'>
<footer class="footer">
<div class="footer-cols">
<ul>
<h6>"ONLY FOCUS ON YOUR GOALS"</h6>
</ul>
    <ul>
        <h5>About</h5>
        <li><a href="https://simple-resume-practise.netlify.app/" rel="noreferrer" target={"_blank"}>Resume</a></li>
        
    </ul>
    <ul>
    <h5>Contact</h5>
    <li>Email - muskan.sirseit2020@gmail.com</li>

    
</ul>
  
   
</div>
<h6 id = 'key'><div className='social-icons'>    
<a id='jone' href="https://www.linkedin.com/in/muskan-sirse-b97651221/" rel="noreferrer" target={"_blank"}>
<i class="fa fa-linkedin" aria-hidden="true"></i>
</a>
<a id = 'jony' href="https://app.netlify.com/teams/muskan-sirseit2020-imbjvdo/overview" rel="noreferrer" target={"_blank"}>
<i class="fa fa-share-alt" aria-hidden="true"></i></a>
<a href='https://github.com/muskan-3210' rel="noreferrer" target={"_blank"}>
<i class="fa fa-github" aria-hidden="true"></i></a>
<a href='https://codepen.io/Muskan_007' rel="noreferrer" target={"_blank"}>
  <i class="fa fa-codepen" aria-hidden="true"></i></a>
</div></h6>


</footer></div>   
</div>
    )
}